@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;800&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html,body{font-family:"Montserrat", sans-serif;background-color:#e3e0cf}@media only screen and (min-width: 900px){body{font-size:1.5rem}}@media only screen and (min-width: 1500px){body{font-size:2rem}}

.frontTop{background-color:#016798;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;padding:1rem;padding-bottom:0}.fullscreen{animation:make-fullscreen forwards 500ms ease}@keyframes make-fullscreen{to{height:100vh}}.frontBottom{background-color:#e3e0cf;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;padding:1rem;padding-top:0}.carousel{margin:auto 0;animation:fade-in 700ms forwards}.verse,.passage{color:#e3e0cf;text-align:center;width:100%;margin:0 auto;animation:fade-in 500ms forwards}@keyframes fade-in{from{opacity:0}to{opacity:1}}.frontTitle{color:#e3e0cf;text-align:center;text-transform:uppercase;margin-top:2rem;margin-bottom:0.5rem;font-weight:800;letter-spacing:1px}.details{color:#e3e0cf;text-align:center;margin-bottom:0.5rem}.frontBorder{border:black 2px solid;padding:0.5rem;width:50%;margin-bottom:2rem;margin-top:0.5rem}.frontBorder .frontSubtitle{color:black;text-align:center;font-size:1.1em;letter-spacing:2px}.searchField{width:65%;margin-bottom:1rem}.searchField label{display:block;font-size:0.7em;font-weight:lighter}.searchField input,.searchField select{outline:none;border:none;background-color:#a7a17f;padding:0.5em;width:100%}.searchField input:focus,.searchField select:focus{background-color:#c5bd8f}.searchLane{display:flex}.selectField{width:100%;margin:auto 0.5rem}select{letter-spacing:1px}.searchButton{padding:0.5rem 2rem;outline:none;border:none;background-color:#d75c3a;color:#e3e0cf;font-weight:600}.center{display:flex;flex-direction:column;animation:fade-in 500ms forwards}.searchButton:active{background-color:#db7052}.searchButton:disabled{background-color:#99857f}@media only screen and (min-width: 767px){.frontTop{height:30vh}.verse,.passage{width:75%}.frontBorder{width:30%}}@media only screen and (min-width: 1500px){.searchField{width:40%;margin-bottom:1.2em}.searchField label{display:block;font-size:0.5em;font-weight:lighter}.searchField input,.searchField select{background-color:#a7a17f;font-size:0.5em;width:100%}.frontBorder{width:25%}.verse,.passage{width:85%}.searchButton{padding:0.5rem 2rem;font-size:0.6em;text-transform:uppercase}}

